<template>
    <div class="container">
        <div class="checkbox" v-for="(item,index) in yearList" :key="index">
            <label role="checkbox" aria-checked="true" :class="[yearType.indexOf(index)!==-1?'is-checked':'','el-checkbox'] ">
                <span aria-checked="mixed" :class="[yearType.indexOf(index)!==-1?'is-checked':'','el-checkbox__input']">
                    <span class="el-checkbox__inner"></span>
                    <input name="checkbox" type="checkbox" aria-hidden="true" class="el-checkbox__original" v-model="yearType" :value="item.type">
                </span>
                <span class="el-checkbox__label">{{item.text}}</span>
            </label>
        </div>

    </div>
</template>

<script>
export default {
  data() {
    return {
      yearType: [0],
      yearList: [
        { text: "2013", type: 0 },
        { text: "2014", type: 1 },
        { text: "2015", type: 2 },
        { text: "2016", type: 3 },
        { text: "2017", type: 4 },
        { text: "2018", type: 5 }
      ]
    };
  }
};
</script>

<style scoped lang="scss">
.checkbox {
  display: flex;
  justify-content: center;
}
.el-checkbox {
  color: #606266;
  font-weight: 500;
  font-size: 14px;
  position: relative;
  cursor: pointer;
  display: inline-block;
  white-space: nowrap;
  user-select: none;
  border-radius: 6px;
  padding:2px 6px;
  .el-checkbox__input {
    white-space: nowrap;
    cursor: pointer;
    outline: none;
    display: inline-block;
    line-height: 1;
    position: relative;
    vertical-align: middle;
  }
}
.el-checkbox__input.is-checked .el-checkbox__inner {
  background-color: #fff;
  background-image: url("../assets/images/checkBox_close.png");
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: center;
  border: none;
    width: 16px;
  height: 16px;
}
.el-checkbox__inner {
  display: inline-block;
  position: relative;
  border: 2px solid #9b9b9b;
  border-radius: 5px;
  box-sizing: border-box;
  width: 18px;
  height: 18px;
  background-color: #fff;
  z-index: 1;
  transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46),
    background-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46);
  background-image: url("../assets/images/checkBox_add.png");
  background-size: 100%;
}
.el-checkbox__inner:after {
  box-sizing: content-box;
  content: "";
  border: 1px solid #fff;
  border-left: 0;
  border-top: 0;
  height: 7px;
  left: 4px;
  position: absolute;
  top: 1px;
  transform: rotate(45deg) scaleY(0);
  width: 3px;
  transition: transform 0.15s ease-in 0.05s;
  transform-origin: center;
}
.el-checkbox__original {
  opacity: 0;
  outline: none;
  position: absolute;
  margin: 0;
  width: 0;
  height: 0;
  z-index: -1;
}

.el-checkbox__input.is-checked + .el-checkbox__label {
  color: #fff;
}
.is-checked {
  box-sizing: border-box;
  background-color: #32adfe;
}
.el-checkbox__label {
  display: inline-block;
  padding-left: 10px;
  line-height: 19px;
  font-size: 14px;
}
</style>
